@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-self: stretch;
          align-self: stretch;
  margin: 1rem 0;
}

.menu-nav {
  width: 100%;
  /* border: 1px solid black;
  background-color: hsl(var(--primary-dark-violet));
  border-radius: 10px;
  justify-self: center;
  position: absolute;
  top: 5rem;
  margin: 0.5rem 0;
  animation: fadein 0.3s ease-out; */
}

.menu-nav-mobile {
  border: 1px solid black;
  background-color: hsl(var(--primary-dark-violet));
  border-radius: 10px;
  /* overlay the mobile nav and center it */
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  margin-left: 1rem;
  margin-right: 1rem;
  -webkit-animation: fadein 0.3s ease-out;
          animation: fadein 0.3s ease-out;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.nav-bar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-nav-mobile li {
  text-align: center;
  margin: 2rem 0;
}

.menu-nav-mobile a {
  text-decoration: none;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.menu-nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.menu-nav li {
  margin: 0 1rem;
}

.menu-nav a {
  text-decoration: none;
  color: hsla(var(--neutral-very-dark-blue), 0.7);
  font-weight: 700;
}

.menu-nav a:hover {
  color: hsl(var(--neutral-very-dark-blue));
}

.menu-nav a:visited {
  /* color: var(--neutral-very-dark-blue); */
}

.menu-nav hr {
  display: none;
}

.nav-logo {
  /* margin: 0; */
  margin-right: 2rem;
}

.menu-icon svg {
  opacity: 0.5;
}

hr {
  height: 1px;
  border: 0;
  background-color: hsla(var(--primary-dark-violet), 1);
  -webkit-filter: brightness(140%);
          filter: brightness(140%);
  width: 85%;
}

.menu-nav .nav-item-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.menu-nav .account-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 1rem;
}

.btn {
  border: none;
  text-align: center;
}

.btn:active {
  outline: none;
}

.btn--round {
  border-radius: 25px;
}

.btn--regular {
  border-radius: 10px;
}

/* size */

.btn--mobile {
  width: 80%;
  height: 2.5rem;
}

.btn--small {
  width: 100px;
  height: 2rem;
}

.btn--medium {
  width: 200px;
  height: 3rem;
}

.btn--long {
  width: 90%;
  height: 2.5rem;
}

.cyan {
  background-color: hsl(var(--primary-cyan));
  color: white;
}

.violet {
  background-color: hsl(var(--primary-dark-violet));
  color: white;
}

.cyan:hover {
  background-color: hsla(var(--primary-cyan), 0.5);
  transition: all 0.3s ease;
}

:root {
  --primary-cyan: 180, 66%, 49%;
  --primary-dark-violet: 257, 27%, 26%;
  --secondary-red: 0, 87%, 67%;
  --neutral-gray: 0, 0%, 75%;
  --neutral-grayish-violet: 257, 7%, 63%;
  --neutral-very-dark-blue: 255, 11%, 22%;
  --neutral-very-dark-violet: 260, 8%, 14%;
  --button-hover: hsla(180, 66%, 49%, 0.9);
}

* {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  box-sizing: border-box;
}

body {
  background: linear-gradient(white 50%, hsla(0, 0%, 75%, 0.5) 50%);
  background: linear-gradient(white 50%, hsla(var(--neutral-gray), 0.5) 50%);
}

.container {
  margin: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

.image {
  width: 100%;
  /* overflow: hidden; */
  /* width: 80%; */
  margin: 1rem 0;
}

img {
  width: 100%;
  height: auto;
}

.top-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.svg-working {
  overflow: visible;
}

.shorten-svg,
.boost-svg {
  background-color: hsl(var(--primary-dark-violet));
}

.info-box {
  margin: 2rem 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.info-box > h3 {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  margin: 0.5rem 0;
}

.info-box > p {
  color: var(--neutral-grayish-violet);
  text-align: center;
  margin: 0.5rem 0;
}

@media (min-width: 800px) {
  .image {
    -webkit-order: 2;
            order: 2;
  }
}

.link-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  /* height: 200px; */
  background-color: white;
  margin: 1rem 0;
  padding: 0.5rem 0;
  border-radius: 5px;
}

.link-card span {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
  width: 90%;
  margin: 0.5rem 0;
}

.link-short {
  color: hsl(var(--primary-cyan));
}

.link-card hr {
  background-color: hsl(var(--neutral-grayish-violet));
  height: 1px;
  border: none;
  width: 100%;
}

.shorten-form {
  background: url(/fm-challenge9/static/media/bg-shorten-mobile.9745a66a.svg)
    hsl(var(--primary-dark-violet)) no-repeat;
  /* background-color: hsl(var(--primary-dark-violet)); */
  background-size: 400px 250px;
  width: 100%;
  height: 200px;
  /* transform: scale(0.75); */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.shorten-form input {
  border-radius: 5px;
  width: 80%;
  height: 3rem;
  padding: 0 1rem;
}

/* .shorten-form [name="url"] {
} */

input:invalid {
  color: hsl(var(--secondary-red));
  /* opacity: 0.4; */
  border-color: hsl(var(--secondary-red));
}

.shorten-form [type="submit"] {
  background-color: hsl(var(--primary-cyan));
  color: white;
  font-weight: 700;
}

.error-msg {
  font-size: 12px;
  color: hsl(var(--secondary-red));
  font-style: italic;
  width: 80%;
}

